@use 'styles/sass' as *;

.phase-announcement-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1100;
  backdrop-filter: blur(15px);
}

.phase-announcement-wrapper {
  width: 100%;
  height: 100vh;
  height: 100dvh;
  display: grid;
  place-items: center;
}

.phase-announcement {
  display: flex;
  flex-direction: column;
  background-color: rgba(white, 0.9);
  padding: get-unit(4 8);
  border-radius: get-unit(4);
  min-width: 40vw;
  max-width: 60vw;

  &__icon {
    display: flex;
    flex-direction: column;
    height: 40vh;
    max-height: 200px;
    margin-bottom: get-unit(1);

    @include md {
      max-height: 150px;
    }

    @include xs {
      max-height: 100px;
    }
  }
}
